.navigation{
    margin: 0;
    display: block;
    position: fixed;
    top: 0;
    left: 100%;
    width: 0;
    height: 100vh;
    overflow: hidden;
    background-color: rgba(29, 20, 29, 0.6);
    z-index: 310;
    animation-fill-mode: forwards;
    animation-delay: 0 !important;
    
}
.navigation.open{
    left: 100%;
    width: 0%;
    overflow: hidden;
    animation-duration: 1s;
    animation-name: slidein;
    animation-delay: 0 !important;

   
    

}



.navigation.close{
    left: 0;
    width: 100%;
    overflow: hidden;
    animation-duration: 1s;
    animation-name: slideout;
    animation-delay: 0 !important;
}
.navigation-body{
    margin-left: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 540px;
    height: 100vh;
    background: #fff;
    padding: 110px 90px 10px 90px;
    overflow: hidden;
    animation-duration: 1s;
    animation-name: slidein;
    animation-delay: 0 !important;
}


.nav{
    
    &-container{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    &-link {
        position: relative;
        display: block;
        padding: 20px 0;
        font-size: 18px;
        line-height: 32px;
        font-weight: 700;
        color: #000;
        &:hover {
            transition: transform 0.2s ease;
        }
        &.active{
            &::before {
                content: "";
                position: absolute;
                left: -70px;
                top: 50%;
                transform: translateX(-50%);
                margin-top: -1px;
                height: 2px;
                width: 0;
                background-color: #000;
                transition: width .2s 1s;
                width: 90px;
            }
            
        }
    }
    &-mobile{
        display: flex;
        flex-direction: column;
        &-num {
            font-size:  var(--fz-xl);
            color: var(--c-primary);
            font-weight: 700;
            display: inline !important;
            transition: color 0.4s ease-in-out;
            &:hover{
                color: var(--c-secondary);
            }
            &-last{
                margin-bottom: 15px !important;
            }
        }
        &-mail{
            margin-bottom: 85px;
            font-size:  var(--fz-m);
            color: #808080;
            font-weight: 700;
        }
    }
    
}





@keyframes hidden {
    from {
       opacity: 1;
    }
  
    to {
        opacity: 0;
    }
}
@keyframes show {
    from {
       opacity: 0;
    }
  
    to {
        opacity: 1;
    }
}
@keyframes slidein {
    from {
        width: 0;
        left: 100%;
    }
  
    to {
        width: 100%;
        left: 0;
    }
}

@keyframes slideout {
    from {
        width: 100%;
        left: 0;
    }
  
    to {
        width: 0;
        left: 100%;
    }
}

.bla{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    cursor: pointer;
    z-index: 10;
    transition: all 0.3s ease-in-out;
}
.line {
    width: 100%;
    height: 2px;
    background: #fff;
    margin: auto 0;
    z-index: 10;
    transition: all 0.4s ease-in-out;   
}


@media (max-height: 650px) {
    .nav-mobile-mail{
        margin-bottom: 35px;
    }
    .nav-link{
        padding: 10px 0;
    }
}

@media (max-height: 550px) {
    .nav-link{
       padding: 10px 0;
    }
}
@media (max-width: 567px) {
    .navigation-body{
        padding: 40px;
    }
}